
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  border: none;
}
html, body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
button {
  cursor: pointer;
}
a {
  position: relative;
  text-decoration: none;
  color: #3F51B5;
  cursor: pointer;
}
a::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background-color: #3F51B5;
  transform: scaleX(0);
  transition: .3s ease-in-out;
}
a:hover::after {
  transform: scaleX(1);
  transition: .3s ease-in-out;
}
@keyframes loading {
from {
    transform: rotate(0);
}
to {
    transform: rotate(360deg);
}
}
pre, code {
  display: block;
  overflow: auto;
  background: #f4f4f4;
  padding: 5px 10px;
  border: 1px solid #eee;
  word-wrap: break-word;
  /* Internet Explorer 5.5+ */
  white-space: pre-wrap;
  /* Firefox */
}
i, li {
  cursor: pointer;
}
.markdown-text {
  width: 100%;
  border-radius: 0 0 4px 4px;
  padding: 5px 8px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  margin-bottom: 10px;
  background-color: white;
}
.markdown-text li {
    list-style: none;
}
.markdown-text img {
    width: 95%;
}
.markdown-text li, .markdown-text p {
    text-indent: 1em;
    margin-bottom: 6px;
}
.markdown-text h1 {
    font-size: 100%;
}
.markdown-text h2 {
    font-size: 98%;
}
.markdown-text h3 {
    font-size: 96%;
}
.markdown-text h4 {
    font-size: 94%;
}
.markdown-text h5 {
    font-size: 92%;
}
.markdown-text table {
    width: 100%;
    border-radius: 4px;
    margin-bottom: 6px;
    border-collapse: collapse;
}
.markdown-text table th {
      border: none;
      background-color: #009688;
      color: white;
}
.markdown-text table tr:nth-of-type(odd) {
      background-color: #E0E0E0;
      border: none;
}
.markdown-text table tr:nth-of-type(even) {
      background-color: #EFEBE9;
      border: none;
}
.markdown-text table td {
      max-width: 120px;
      padding: 0 4px;
      font-size: 80%;
      overflow: auto;
}
@media screen and (min-width: 760px) {
.markdown-text {
    padding: 10px 50px;
}
}
#star {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.slide-fade-enter-active {
  transition: all .6s ease;
}
.slide-fade-leave-active {
  transition: all .5s ease;
}
.slide-fade-enter, .slide-fade-leave-active {
  transform: translateX(60px);
  opacity: 0;
}
.slide-left-enter-active {
  transition: all .3s ease;
}
.slide-left-leave-active {
  transition: all .2s ease-out;
}
.slide-left-enter, .slide-left-leave-active {
  transform: translateX(-150px);
  opacity: 0;
}
.slide-top-enter-active {
  transition: all .5s ease;
}
.slide-top-leave-active {
  transition: all .2s ease-out;
}
.slide-top-enter, .slide-top-leave-active {
  transform: translateY(-5px);
  opacity: 0;
}
.show-enter-active {
  transition: all .3s ease;
}
.show-leave-active {
  transition: all .2s ease-in;
}
.show-enter, .show-leave-active {
  transform: translateX(200px);
  opacity: 0;
}
#app {
  position: relative;
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}



